/* CSS */
.project_button_1 {
  animation: 1.5s ease infinite alternate running shimmer;
  background: linear-gradient(90deg, var(--color-yellow) 0%, var(--color-blue) 30%, var(--color-lavender)  85%);
  background-size: 200% 100%;
  border: none;
  border-radius: 6px;
  box-shadow: -2px -2px 10px rgba(255, 227, 126, 0.5), 2px 2px 10px rgba(144, 148, 255, 0.5);
  color: #170F1E;
  cursor: pointer;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 670;
  line-height: 24px;
  overflow: hidden;
  padding: 12px 24px;
  position: relative;
  text-decoration: none;
  transition: 0.2s;
}

.project_button_1 svg {
  left: -20px;
  opacity: 0.5;
  position: absolute;
  top: -2px;
  transition: 0.5s cubic-bezier(0.5, -0.5, 0.5, 1.5);
}
.project_button_1:hover svg {
  opacity: 0.8;
  transform: translateX(50px) scale(1.5);
}
.project_button_1:hover {
  transform: rotate(-3deg);
}
.project_button_1:active {
  transform: scale(0.95) rotate(-3deg);
}

@keyframes shimmer {
  to {
    background-size: 100% 100%;
    box-shadow: -2px -2px 6px rgba(255, 227, 126, 0.5), 2px 2px 6px rgba(144, 148, 255, 0.5);
  }
}

